import React, { Component } from 'react';
import PropTypes from 'prop-types'
import navbarLogo from './madison-logo-small.png';

class SiteHeader extends Component {
  render() {
    console.log('PROP', this.props.user)
    return (
      <header id="site-header">
        <div className="navbar navbar-inverse">
          <div className="container-fluid">

              <div className="navbar-header">
                <a href="/" className="navbar-brand">
                  <img
                    src={navbarLogo}
                    alt="Madison's Lumber"
                    className="navbar-logo"
                  />
                  Home
                </a>
              </div>


                {this.props.user
                  ? <ul className="nav navbar-nav">
                      <li>
                        <a href="/mills">
                        <i className="fa fa-database" aria-hidden="true"></i> Mills</a>
                      </li>
                      <li>
                        <a href="/users">
                        <i className="fa fa-users" aria-hidden="true"></i> Manage Users</a>
                      </li>
                      <li>
                        <a href="/logout">
                        <i className="fa fa-sign-out" aria-hidden="true"></i> logout</a>
                      </li>
                    </ul>
                  : <ul className="nav navbar-nav">
                      <li>
                          <a href="/login">
                          <i className="fa fa-sign-in" aria-hidden="true"></i> login
                        </a>
                      </li>
                    </ul>}



            </div>
          </div>
      </header>
    );
  }
}

SiteHeader.propTypes = {
  user: PropTypes.string.isRequired
}

export default SiteHeader;


