import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import api from '../utils/api';
import SiteHeader from './SiteHeader';
import Home from './Home';
import Login from './Login';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      token: null,
      userName: '',
    }
    this.authUser = this.authUser.bind(this)
  }
  componentDidMount() {
    this.updateToken(this.state.validToken);
  }
  updateToken(token) {
    if(token) {
      console.log('you got a token')
      return
    }
    api.fetchToken().then( res => {
      console.log(res)
    })
  }

  authUser(token, userName) {
    this.setState(()=> ({
      token,
      userName
    }));
  }

  render() {
    return (
      <Router>
        <div className="App">
          <SiteHeader user={this.state.userName}/>
          <main id="site-main">
            <Switch>
              <Route exact path='/' component={Home} />
              <Route path='/login' render={(props) => (
                <Login {...props} onSubmit={this.authUser} />
              )}/>
              <Route render={ () => <p><strong>404 - Not Found</strong></p> } />
            </Switch>
          </main>
          <footer id="site-footer">
            Copyright &copy; 2017 <a href="http://www.ketadesign.ca/">
              KetaDesign Productions Inc.
            </a>
          </footer>
        </div>
      </Router>
    );
  }
}

export default App;



